@use '../../variable.scss';

.section-footer {
    padding: 40px 0;
    height: auto;

    .copyright {
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        gap: 15px;
        padding-bottom: 20px;
        width: 100%;
        height: 124px;

        .logo {
            width: 100%;
            height: 124px;
            object-fit: cover;
        }
    }

    p {
        margin: 0;
        color: variable.$secondColor;
        text-align: center;
    }
}